import { Col, Row, Typography } from "antd";
import { useRouteMatch } from "react-router";
import BreadcrumHeader from "../../components/breadcrumHeader";

interface Props {}

export const ProjectDashboard = (props: Props) => {
  const route = useRouteMatch<{ projectSlug: string }>();
  console.log(route);

  return (
    <>
      <BreadcrumHeader projectName="TCM" routeName="dashboard" />
      <br />
      <Row>
        <Col span="24">
          <Typography.Text type="secondary">
            Project Slug: {route.params?.projectSlug}
          </Typography.Text>
        </Col>
      </Row>
    </>
  );
};

export default ProjectDashboard;
